.wrapper {
  border: 1px solid black;
  max-width: 400px;
  padding: 16px;
  margin: auto;
}

header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid black;
  padding-bottom: 16px;
}

header .logo {
  font-size: 24px;
  font-weight: bold;
}

.list-item {
  border: 1px solid #ccc;
  padding: 8px;
  margin-top: 16px;
}

.list-item:hover {
  outline: 2px solid lightskyblue;
  cursor: pointer;
}

dialog .title {
  font-size: 18px;
  font-weight: bold;
}

dialog div:not(.title) {
  margin-top: 16px;
}

dialog input {
  width: 100%;
  height: 32px;
  padding: 8px;
}

dialog .dialog-actions {
  display: flex;
  justify-content: space-between;
}